<template>
  <span class="mobile-filter-button" v-on:click="showFilter">Фильтр поиска</span>
  <div class="left-bar-wrap car-filter" :class="filterStatus && 'active'">
    <h1 class="left-bar-title">Параметры поиска авто</h1>
    <div class="form-group">
      <label class="search-label">Марка</label>
      <select class="search-select" v-model="brand">
        <option value="">Все марки</option>
        <option v-for="brand in brands" :key="brand" :value="brand.id">{{ brand.title }}</option>
      </select>
    </div>
    <div class="form-group pb-4 pt-4">
      <label class="search-label">Модель</label>
      <span class="model-tooltip">Зажмите CTRL чтобы выбрать несколько моделей</span>
      <select class="search-select multiselect" v-model="model" multiple>
        <option v-for="model in models" :key="model" :value="model.id">{{ model.title }} ({{ model.count }})</option>
      </select>
    </div>
    <div class="form-group">
      <label class="search-label">Пробег</label>
      <select class="search-select" @change="chooseMileage">
        <option value="">---</option>
        <option value="0-25000">0 - 25 000</option>
        <option value="25000-50000">25 000 - 50 000</option>
        <option value="50000-75000">50 000 - 75 000</option>
        <option value="75000-100000">75 000 - 100 000</option>
        <option value="100000-150000">100 000 - 150 000</option>
        <option value="150000-200000">150 000 - 200 000</option>
        <option value="200000">> 200 000</option>
      </select>
    </div>
    <div class="form-group">
      <label class="search-label">Тип ТС</label>
      <select class="search-select" v-model="body">
        <option value="">---</option>
        <option v-for="body in bodyList" :key="body" :value="body.id">{{ body.title }}</option>
      </select>
    </div>
    <div class="form-group">
      <label class="search-label">Расположение</label>
      <select class="search-select" v-model="location">
        <option value="">---</option>
        <option v-for="location in cityList" :key="location" :value="location.id">{{ location.location }}</option>
      </select>
    </div>
    <div class="form-group">
      <label class="search-label">Топливо</label>
      <select class="search-select" v-model="fuel_type">
        <option value="">---</option>
        <option value="Flexible Fuel">Бензин</option>
        <option value="Diesel">Дизель</option>
        <option value="Gasoline">Газ</option>
        <option value="Hybrid">Гибрид</option>
        <option value="Electric">Электрический</option>
        <option value="PlugInHybrid">Подключаемый гибрид</option>
        <option value="Other">Другое</option>
      </select>
    </div>
    <div class="form-group">
      <label class="search-label">трансмиссия</label>
      <select class="search-select" v-model="transmission">
        <option value="">---</option>
        <option value="AUTOMATIC">Автомат</option>
        <option value="MANUAL">Механика</option>
      </select>
    </div>
    <div class="form-group">
      <label class="search-label">Цвет кузова</label>
      <select class="search-select" v-model="color_f">
        <option value="">---</option>
        <option v-for="color in colorList" :key="color" :value="color.id">{{ color.title }}</option>
      </select>
    </div>
    <div class="form-group mt-4">
      <label class="search-label">Год от</label>
      <select class="search-select" v-model="yearFrom">
        <option value="">---</option>
        <option v-for="i in 24" :key="i" :value="year(i)" :selected="(year(i) == yearFrom) && 'selected'">{{
            year(i)
          }}
        </option>
      </select>
    </div>
    <div class="form-group border-bottom pb-4 mb-3">
      <label class="search-label">Год до</label>
      <select class="search-select" v-model="yearTo">
        <option value="">---</option>
        <option v-for="i in 24" :key="i" :value="year(i)" :selected="(year(i) == yearTo) && 'selected'">{{
            year(i)
          }}
        </option>
      </select>
    </div>
    <div class="d-flex justify-content-center">
      <a class="custom-button" v-on:click="search">Поиск</a>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      auction: this.$route.query['auction'],
      brand: this.$route.query['brand'],
      model: this.$route.query['model'],
      yearMin: 1999,
      yearMax: 2023,
      yearFrom: this.$route.query['max_year'],
      yearTo: this.$route.query['min_year'],
      priceFrom: '',
      priceTo: '',
      filterStatus: false,
      mileage_from: 0,
      mileage_to: 1000000000,
      body: '',
      location: '',
      fuel_type: '',
      transmission: '',
      color_f: '',
    }
  },
  computed: {
    brands() {
      return this.$store.getters.getBrandList
    },
    models() {
      return this.$store.getters.getModelList
    },
    bodyList() {
      return (this.$store.getters.getCalculatorBodyList)
          ? this.$store.getters.getCalculatorBodyList
          : this.$store.dispatch('getCalculatorBodyList')
    },
    cityList() {
      return (this.$store.getters.getCityList)
          ? this.$store.getters.getCityList
          : this.$store.dispatch('getCityList', this.auction || '')
    },
    colorList() {
      return (this.$store.getters.getColorList)
          ? this.$store.getters.getColorList
          : this.$store.dispatch('getColorList')
    }
  },
  watch: {
    $route() {
      this.brand = this.$route.query['brand']
      this.model = this.$route.query['model']
      this.yearFrom = this.$route.query['min_year']
      this.yearTo = this.$route.query['max_year']
    },
    value() {
      this.priceFrom = this.value[0]
      this.priceTo = this.value[1]
    },
    brand() {
      if (this.brand !== "") {
        this.model = ''
        this.$store.dispatch('getModelList', this.brand)
        this.model = this.$route.query['model'] ? this.$route.query['model'] : ''
      }
    }
  },
  methods: {
    year(count) {
      return this.yearMin + +count
    },
    search() {
      var query = {
        auction: this.auction ? this.auction : '',
        brand: this.brand ? this.brand : '',
        model: this.model ? this.model?.join(',') : '',
        'min_year': this.yearFrom ? this.yearFrom : '',
        'max_year': this.yearTo ? this.yearTo : '',
        priceFrom: this.priceFrom ? this.priceFrom : '',
        priceTo: this.priceTo ? this.priceTo : '',
        mileage_from: this.mileage_from || 0,
        mileage_to: this.mileage_to || 1000000000,
        body: this.body || '',
        location: this.location || '',
        fuel_type: this.fuel_type || '',
        transmission: this.transmission || '',
        color_f: this.color_f || '',
      }
      var search = new URLSearchParams(query)
      this.$router.push('/auction?' + search)
    },
    showFilter() {
      this.filterStatus = !this.filterStatus && true
    },
    chooseMileage(e) {
      const mileageArray = e.target.value.split('-')

      this.mileage_from = mileageArray[0]
      this.mileage_to = mileageArray[1] || 1000000000
    },
  }
}
</script>

<style scoped>
.left-bar-wrap .multiselect {
  height: 250px;
  overflow: auto;
}

@media screen and (max-width: 720px){
  .left-bar-wrap .multiselect {
    height: 50px;
  }

  .model-tooltip {
    display: none;
  }
}
</style>